*{
    font-family: sans-serif, Arial;
}

body{
    padding: 60px;
}

button{
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    font-weight: 600;
    padding: 2% 3% 2% 3%;
    outline: none;
}

.primary{
    background-color: rgb(12 124 204);
    color: #fff;
}
.primary:hover{
    background-color: rgb(12 124 204 / 74%);
    color: #fff;
}

input{
    background-clip: padding-box;
    background-color: #fff;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    color: #495057;
    display: block;
    height: calc(1.5em + .75rem + 2px);
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    padding: .375rem .75rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    width: 100%;
}

input:focus{
    background-color: #fff;
    border-color: #80bdff !important;
    box-shadow: 0 0 0 0.2rem rgba(0,123,255,.25);
    color: #495057;
    outline: 0;
}

.card-content{
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
}

.category{
    width: 100%;
}

.card{
    align-content: center;
    border: 1px solid gray;
    border-radius: 6px;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
    width: 20%;
}

.card-title{
    font-weight: 700;
    text-align: center;
}

.card-img{
    display: block;
    width: 100%;
}

